import { newSpecPage } from "@stencil/core/testing";
import { RwcSpinner } from "../spinner";

describe("RwcSpinner", () => {
  it("should render rwc-folding-squares component", async () => {
    const page = await newSpecPage({
      components: [RwcSpinner],
      html: `<rwc-spinner></rwc-spinner>`,
    });

    expect(page.root).toEqualHtml(`
      <rwc-spinner>
        <mock:shadow-root>
          <div class="spinner"></div>
        </mock:shadow-root>
      </rwc-spinner>
    `);
  });
});
